import * as React from 'react';
import { render } from '@testing-library/react';
import Zhi from './reader';
import card from './fixtures/card.json';

describe('Zhi:card', () => {
  it('正确渲染', () => {
    const asl = ['root', card];
    const container = render(<Zhi content={asl} />);
    expect(container).toMatchSnapshot();
    expect(!!container.getByTestId('demo')).toBe(true);
  });
});
